import React from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Button from 'react-bootstrap/Button';
import Form from 'react-bootstrap/Form';
import Card from 'react-bootstrap/Card';
import InvoiceItem from './InvoiceItem';
import InvoiceModal from './InvoiceModal';
import InputGroup from 'react-bootstrap/InputGroup';

class InvoiceForm extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isOpen: false,
      currency: '₹',
      currentDate: '',
      invoiceNumber: '',
      dateOfIssue: '',
      billTo: '',
      billToEmail: '',
      billToAddress: '',
      billFrom: '',
      billFromEmail: '',
      billFromAddress: '',
      notes: '',
      total: '0.00',
      subTotal: '0.00',
      taxRate: '',
      taxAmmount: '0.00',
      discountRate: '',
      discountAmmount: '0.00',
      companyGst: '',
      natureOfWork: '',
      vendorCode: '',
      woNumber: '',
      vendorPan: '',
      vendorGst: '',
      items: [
        {
          id: (+ new Date() + Math.floor(Math.random() * 999999)).toString(36),
          description: '',
          price: '1.00',
          unit: '',
          quantity: 1
        }
      ]
    };
  }
  componentDidMount(prevProps) {
    this.handleCalculateTotal()
  }
  handleRowDel = (items) => {
    var index = this.state.items.indexOf(items);
    this.state.items.splice(index, 1);
    this.setState(this.state.items);
    this.handleCalculateTotal()
  };
  handleAddEvent = (evt) => {
    const id = (+ new Date() + Math.floor(Math.random() * 999999)).toString(36);
    const item = {
      id: id,
      price: '1.00',
      description: '',
      unit: '',
      quantity: 1
    }
    this.setState({
      items: [...this.state.items, item]
    });
    this.handleCalculateTotal()
  }
  handleCalculateTotal = () => {
    var items = this.state.items;
    var subTotal = 0;

    items.forEach(item => subTotal += (parseFloat(item.price).toFixed(2) * parseInt(item.quantity).toFixed(2)))
    this.setState({
      subTotal: parseFloat(subTotal).toFixed(2)
    }, () => {
      this.setState({
        taxAmmount: parseFloat(parseFloat(subTotal) * (this.state.taxRate / 100)).toFixed(2)
      }, () => {
        this.setState({
          discountAmmount: parseFloat(parseFloat(subTotal) * (this.state.discountRate / 100)).toFixed(2)
        }, () => {
          this.setState({
            total: ((subTotal - this.state.discountAmmount) + parseFloat(this.state.taxAmmount))
          });
        });
      });
    });

  };
  onItemizedItemEdit = (evt) => {
    var item = {
      id: evt.target.id,
      name: evt.target.name,
      value: evt.target.value
    };
    var items = this.state.items.slice();
    var newItems = items.map(function (items) {
      for (var key in items) {
        if (key === item.name && items.id === item.id) {
          items[key] = item.value;
        }
      }
      return items;
    });
    this.setState({ items: newItems });
    this.handleCalculateTotal();
  };
  editField = (event) => {
    this.setState({
      [event.target.name]: event.target.value
    });
    this.handleCalculateTotal();
  };
  onCurrencyChange = (selectedOption) => {
    this.setState(selectedOption);
  };
  openModal = (event) => {
    event.preventDefault()
    this.handleCalculateTotal()
    this.setState({ isOpen: true })
  };
  closeModal = (event) => this.setState({ isOpen: false });
  render() {
    return (<Form onSubmit={this.openModal}>
      <Row>
        <Col md={8} lg={9}>
          <Card className="p-4 p-xl-5 my-3 my-xl-4">
            <div className="d-flex flex-row justify-content-between align-item-baseline mb-3">
              <Row style={{ width: "50%" }}>
                <Col>
                  <Form.Label className="fw-bold">Bill to:</Form.Label>
                  <Form.Control placeholder={"Who is this invoice to?"} rows={3} value={this.state.billTo} type="text" name="billTo" className="my-2" onChange={(event) => this.editField(event)} autoComplete="name" required="required" />
                  <Form.Control placeholder={"Billing address"} value={this.state.billToAddress} type="text" name="billToAddress" className="my-2" autoComplete="address" onChange={(event) => this.editField(event)} required="required" />
                  <Form.Control placeholder={"Email address"} value={this.state.billToEmail} type="email" name="billToEmail" className="my-2" onChange={(event) => this.editField(event)} autoComplete="email" />
                </Col>
                <div className="d-flex flex-row align-items-center mb-2">
                  <span className="fw-bold me-2">Company&nbsp;GST&nbsp;No.:</span>
                  <Form.Control type="text" value={this.state.companyGst} name={"companyGst"} onChange={(event) => this.editField(event)} min="1" style={{
                    maxWidth: '100%'
                  }} required="required" />
                </div>
                <div className="d-flex flex-row align-items-center mb-2">
                  <span className="fw-bold me-2">Nature&nbsp;of&nbsp;work:</span>
                  <Form.Control type="text" value={this.state.natureOfWork} name={"natureOfWork"} onChange={(event) => this.editField(event)} min="1" style={{
                    maxWidth: '100%'
                  }} required="required" />
                </div>
              </Row>
              <Row style={{ width: "50%" }}>
                <Col>
                  <div className="d-flex flex-row align-items-center mb-2">
                    <span className="fw-bold d-block me-2">Invoice&nbsp;Date:</span>
                    <Form.Control type="date" value={this.state.dateOfIssue} name={"dateOfIssue"} onChange={(event) => this.editField(event)} style={{
                      maxWidth: '100%'
                    }} required="required" />
                  </div>
                  <div className="d-flex flex-row align-items-center mb-2">
                    <span className="fw-bold me-2">Invoice&nbsp;No.:&nbsp;</span>
                    <Form.Control type="text" value={this.state.invoiceNumber} name={"invoiceNumber"} onChange={(event) => this.editField(event)} min="1" style={{
                      maxWidth: '100%'
                    }} required="required" />
                  </div>
                  <div className="d-flex flex-row align-items-center mb-2">
                    <span className="fw-bold d-block me-2">Vendor&nbsp;Code:</span>
                    <Form.Control type="text" value={this.state.vendorCode} name={"vendorCode"} onChange={(event) => this.editField(event)} style={{
                      maxWidth: '100%'
                    }} required="required" />
                  </div>
                  <div className="d-flex flex-row align-items-center mb-2">
                    <span className="fw-bold d-block me-2">WO&nbsp;No.:</span>
                    <Form.Control type="text" value={this.state.woNumber} name={"woNumber"} onChange={(event) => this.editField(event)} style={{
                      maxWidth: '100%'
                    }} required="required" />
                  </div>
                  <div className="d-flex flex-row align-items-center mb-2">
                    <span className="fw-bold d-block me-2">PAN:</span>
                    <Form.Control type="text" value={this.state.vendorPan} name={"vendorPan"} onChange={(event) => this.editField(event)} style={{
                      maxWidth: '100%'
                    }} required="required" />
                  </div>
                  <div className="d-flex flex-row align-items-center mb-2">
                    <span className="fw-bold d-block me-2">GST:</span>
                    <Form.Control type="text" value={this.state.vendorGst} name={"vendorGst"} onChange={(event) => this.editField(event)} style={{
                      maxWidth: '100%'
                    }} required="required" />
                  </div>
                </Col>
              </Row>
            </div>
            <InvoiceItem onItemizedItemEdit={this.onItemizedItemEdit} onRowAdd={this.handleAddEvent} onRowDel={this.handleRowDel} currency={this.state.currency} items={this.state.items} />
            <Row className="mt-4 justify-content-end">
              <Col lg={6}>
                <div className="d-flex flex-row align-items-start justify-content-between">
                  <span className="fw-bold">Subtotal:
                  </span>
                  <span>{this.state.currency}
                    {this.state.subTotal}</span>
                </div>
                <div className="d-flex flex-row align-items-start justify-content-between mt-2">
                  <span className="fw-bold">Discount:</span>
                  <span>
                    <span className="small ">({this.state.discountRate || 0}%)</span>
                    {this.state.currency}
                    {this.state.discountAmmount || 0}</span>
                </div>
                <div className="d-flex flex-row align-items-start justify-content-between mt-2">
                  <span className="fw-bold">Tax:
                  </span>
                  <span>
                    <span className="small ">({this.state.taxRate || 0}%)</span>
                    {this.state.currency}
                    {this.state.taxAmmount || 0}</span>
                </div>
                <hr />
                <div className="d-flex flex-row align-items-start justify-content-between" style={{
                  fontSize: '1.125rem'
                }}>
                  <span className="fw-bold">Total:
                  </span>
                  <span className="fw-bold">{this.state.currency}
                    {this.state.total || 0}</span>
                </div>
              </Col>
            </Row>
          </Card>
        </Col>
        <Col md={4} lg={3}>
          <div className="sticky-top pt-md-3 pt-xl-4">
            <Button variant="primary" type="submit" className="d-block w-100">Review Invoice</Button>
            <InvoiceModal showModal={this.state.isOpen} closeModal={this.closeModal} info={this.state} items={this.state.items} currency={this.state.currency} subTotal={this.state.subTotal} taxAmmount={this.state.taxAmmount} discountAmmount={this.state.discountAmmount} total={this.state.total} />
            <Form.Group className="mb-3">
              <Form.Label className="fw-bold">Currency:</Form.Label>
              <Form.Select onChange={event => this.onCurrencyChange({ currency: event.target.value })} className="btn btn-light my-1" aria-label="Change Currency">
                <option value="₹">INR (Indian Nation Rupee)</option>
                <option value="£">GBP (British Pound Sterling)</option>
                <option value="$">USD (United States Dollar)</option>
              </Form.Select>
            </Form.Group>
            <Form.Group className="my-3">
              <Form.Label className="fw-bold">Tax rate:</Form.Label>
              <InputGroup className="my-1 flex-nowrap">
                <Form.Control name="taxRate" type="number" value={this.state.taxRate} onChange={(event) => this.editField(event)} className="bg-white border" placeholder="0.0" min="0.00" step="0.01" max="100.00" />
                <InputGroup.Text className="bg-light fw-bold text-secondary small">
                  %
                </InputGroup.Text>
              </InputGroup>
            </Form.Group>
            <Form.Group className="my-3">
              <Form.Label className="fw-bold">Discount rate:</Form.Label>
              <InputGroup className="my-1 flex-nowrap">
                <Form.Control name="discountRate" type="number" value={this.state.discountRate} onChange={(event) => this.editField(event)} className="bg-white border" placeholder="0.0" min="0.00" step="0.01" max="100.00" />
                <InputGroup.Text className="bg-light fw-bold text-secondary small">
                  %
                </InputGroup.Text>
              </InputGroup>
            </Form.Group>
          </div>
        </Col>
      </Row>
    </Form>)
  }
}

export default InvoiceForm;
